// mega menu builder button warper 
.ekit-menu-mega-submenu{
    font-size: 12px;
    display: block;
    margin-top: 5px;

    &.enabled_item{
        color: #81c784;
        display:none;
    }
    &.disabled_item{
        color: #aaa;
    }
}

#ekit-menu-builder-warper{
    text-align: center;
    .enabled_item{
        display: none;
    }
    img{
        height: 30px;
        margin-right: 5px;
    }
    .button{
        height: 46px;
        font-size: 16px;
        text-transform: uppercase;
        margin: auto;
        margin-top: 25px;
    }
    &.is_enabled{
        .disabled_item{ display: none; }
        .enabled_item{ display: block; }
    }
}

#ekit-menu-item-settings-modal{
    .wp-picker-holder{
        position: relative;
    }
    .wp-picker-container .iris-picker{
        position: absolute;
    }
    .button.wp-color-result{
        margin-right: 0;
    }
    .wp-picker-container .wp-color-result.button{
        height: 30px;
    }
    .wp-color-result-text{
        line-height: 28px;
    }
    .option-table{
        width: 100%;
        margin: auto;
        max-width: 400px;
    }
    .icons-selector .selector {
        color: #555;
        border-color: #ccc;
        background: #f7f7f7;
        box-shadow: 0 1px 0 #ccc;
        border-radius: 3px;
        width: 101px;
    }
    .icons-selector .selector-popup{
        right: 0;
    }

    
    .icons-selector .selector-button {
        display: block;
        text-align: right;
        width: 100%;
        background: transparent!important;
        position: absolute;
    }
}

#ekit-menu-builder-modal{
    z-index: 9999;
    .uk-modal-dialog {
        height: 100%;
        width: 100%;
    }
    .uk-close-large {
        right: auto;
        top: 8px;
        left: 8px;
        background: #d30c5c;
        color: #fff;
        width: 25px;
        transition: color 500s linear;

        &:hover{
            color: #aaa;
        }
    }
    #ekit-menu-builder-iframe{
        width: 100%;
        height: 100%;
    }
}